@keyframes Upstream {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
@keyframes HalfUp {
  0% {
    opacity: 0;
    transform: translateY(50%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
@keyframes DownStream {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
@keyframes LeftStream {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}
@keyframes RightStream {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}
@keyframes Upstream70 {
  0% {
    transform: translateY(70%);
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
@keyframes FadeIn {
  0% {
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
#section1,
#section2,
#section3,
#section4 {
  width: auto;
  height: 100vh;
  position: relative;
  z-index: 1;
  margin: 0 15rem;
}

#section1 {
  background-color: transparent;
}
#section1 .content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  background-color: transparent;
  position: absolute;
  top: 0;
  width: 100%;
  height: 92.5vh;
  margin-top: 7.5vh;
}
#section1 .content .profile {
  position: relative;
  margin: 5rem 0;
  border-radius: 5rem;
  background-color: blue;
  animation: HalfUp ease-in-out 0.3s;
}
#section1 .content .photoWrapper {
  background-color: red;
  margin: 5rem 0;
  border-radius: 5rem;
  background-size: cover;
  background-position: center;
  animation: HalfUp ease-in-out 0.5s;
}

